.dropdown {
  position: fixed;
  right: 50px;
  top: 100px;
  width: 186px;
  display: flex;
  flex-direction: column;
  // border-radius: 10px;
  padding: 1rem;
  overflow: hidden;
  transition: height 1s ease;
  border: 1px solid gray;
  background-color: rgba(0, 0, 0, 0.3);
}

.dropdownItem {
  color: white;
  padding-bottom: 5px;
}

.dropdownItem:hover {
  color: white;
  filter: brightness(1.2);
  transform: scale(1.1);
}

@media (max-width: 767px) {
  .dropdown {
    width: 133px;
    font-size: 0.7rem;
  }
}

